* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}
html body {
  padding: 0;
  font-family: "Syed Sans", Arial, Helvetica, sans-serif;
  background-color: #14342B;
  color: #fbe8cc;
}

html {
  scrollbar-width: none !important;
  -ms-overflow-style: none;
}

html body::-webkit-scrollbar {
  display: none;
}

html {
  scrollbar-width: none !important;
  -ms-overflow-style: none;
}

html body::-webkit-scrollbar {
  display: none;
}

main {
  position: relative;
  z-index: 999;
}

canvas {
  display: block;
}

@media (min-width: 1400px) {
  .container {
    max-width: 1650px;
  }
}
h1 {
  font-size: 10rem;
}

p {
  font-size: clamp(12px, 2.03704vh, 600px);
  color: #fff;
}

a,
a[href^=tel],
a[href^=mailto] {
  color: inherit;
  /* Inherit text color of parent element. */
  text-decoration: none;
  /* Remove underline. */
}

.icon:before {
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  margin-right: 5px;
}

.rotate {
  animation: rotating 3s linear infinite;
}

@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.custom-cursor,
.custom-cursor-dot {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  transform: translate(-50%, -50%);
  pointer-events: none;
  opacity: 0;
}

.custom-cursor {
  width: 0;
  height: 0;
  background-color: #e6cf8b;
  box-shadow: 0 0 0 0 transparent, inset 0em -0.3em 0.4em 0.2em rgba(202, 158, 3, 0.6509803922);
  border-radius: 50%;
}

.custom-cursor-dot {
  height: 25px;
  width: 25px;
  border: 2px solid #000;
  border-radius: 50px;
  box-shadow: 0 6px 0 0px #000;
  background-color: #FCFBF5;
}

html.lenis,
html.lenis body {
  height: auto;
}

.lenis.lenis-smooth {
  scroll-behavior: auto !important;
}

.lenis.lenis-smooth [data-lenis-prevent] {
  overscroll-behavior: contain;
}

.lenis.lenis-stopped {
  overflow: hidden;
}

.lenis.lenis-smooth iframe {
  pointer-events: none;
}

.circle {
  background-color: #CDEA68;
  border-radius: 100%;
  font-size: 13px;
  color: #CDEA68;
  box-shadow: 0 0 0 0 #CDEA68;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(206, 234, 104, 0.8);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(252, 3, 3, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(252, 3, 3, 0);
  }
}
#preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff; /* Background color */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.spinner {
  border: 6px solid #f3f3f3;
  border-top: 6px solid #3498db; /* Spinner color */
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
